.custom-embed {
  @apply -mt-px20;

  @screen md {
    @apply -mt-px30;
  }

  @screen xl {
    @apply -mt-px10;
  }

  &__container {
    @apply container mx-auto;

    @screen xl {
      @apply grid tw-grid-desktop;
    }
  }
}
.embed-content {
    @apply relative rounded-px10;

    @screen xl {
      @apply col-span-10 col-start-2;
    }

    > * {
        @apply absolute top-0 left-0 w-full h-full;
    }
}
